import "./styles.css";
import { useState } from "react";

export default function App() {
  const [check, setChecked] = useState([
    {
      label: "A",
      checked: false
    },
    {
      label: "B",
      checked: false
    },
    {
      label: "C",
      checked: false
    },
    {
      label: "D",
      checked: false
    },
    {
      label: "E",
      checked: false
    }
  ]);
  const [allChecked, setAllChecked] = useState(false);

  const handleCheck = (e) => {
    const id = e.target.id;
    const updatedCheck = check.map(({ label, checked }) =>
      id === label ? { label, checked: !checked } : { label, checked }
    );
    setChecked(updatedCheck);
  };

  const toggleAllCheck = () => {
    setAllChecked((pre) => !pre);
    const updatedCheck = check.map(({ label, checked }) => ({
      label,
      checked: allChecked ? false : true
    }));
    setChecked(updatedCheck);
  };

  return (
    <div className="App">
      {check.map(({ label, checked }) => (
        <div>
          <label htmlFor={label}>
            <input
              id={label}
              type="checkbox"
              checked={checked}
              onChange={handleCheck}
            />
            <span>{label}</span>
          </label>
        </div>
      ))}

      <br />
      <button onClick={toggleAllCheck}>
        {allChecked ? "Deselect all " : "Select All "}
      </button>
      <br />
      <p>Selected Labels : </p>

      {check.map(({ label, checked }) => (
        <div>{checked ? label : ""}</div>
      ))}
    </div>
  );
}
